main {
  min-height: calc(100vh - 297px);
}

.footer {
  min-width: 100%;
  background: #f7f7f7;
}

.footer-txt {
  color: #545454;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 30px 36px 28px 48px;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.172;
  display: flex;
}

.heart {
  margin-left: 10px;
  margin-right: 10px;
}

.modal-open {
  border-bottom: 1px solid;
}

@media screen and (min-width: 768px) {
  .footer-txt {
    font-size: 16px;
    line-height: 1.187;
  }
}

.movieList {
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding-bottom: 20px;
  list-style: none;
  display: flex;
}

.movieCard {
  cursor: pointer;
  max-width: 280px;
  height: 470px;
  border-radius: 5px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

.movieCard:hover, .movieCard:focus {
  transform: scale(1.03);
  box-shadow: 6px 2px 16px rgba(136, 165, 191, .48), -6px -2px 16px rgba(255, 255, 255, .8);
}

.movieCard__img {
  width: 280px;
  height: 402px;
}

.card_wrap {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
}

.movieCard__title {
  color: #000;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.movieCard__info {
  color: #ff6b08;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

@media screen and (min-width: 768px) {
  .movieList {
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    gap: 32px;
  }

  .movieCard__img {
    width: 336px;
    height: 455px;
  }

  .movieCard {
    max-width: 336px;
    height: 525px;
  }
}

@media screen and (min-width: 1280px) {
  .movieList {
    gap: 16px;
  }

  .movieCard__img {
    width: 395px;
    height: 574px;
  }

  .movieCard__title, .movieCard__info {
    font-size: 16px;
    line-height: 1.2;
  }

  .movieCard {
    max-width: 395px;
    height: 655px;
  }
}

.cards-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .cards-container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .cards-container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .cards-container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

